.main-catalog
    &__lg-items
        display: flex
        justify-content: space-between
        margin-bottom: 53px

        &-item
            width: 500px
            height: 486px
            border-radius: 30px
            overflow: hidden
            display: flex
            align-items: flex-end
            padding: 0 0 50px 44px
            overflow: hidden
            transition: all .2s linear
            &:hover
                background-position: center
                background-size: 130%
            & span
                color: white
                font-size: 40px
                font-weight: 700
    &__products-buttons
        width: 480px
        display: flex
        justify-content: space-between
        margin-bottom: 40px
        &_item
            font-size: 20px
            line-height: 24px
            padding: 16px 28px
            border-radius: 30px
            color: white
            background: #416EF2
            border: 2px solid #416EF2
            transition: all .2s linear
            &.active, &:hover
                border: 2px solid var(--towhite)
                color: var(--towhite)
                background: transparent
    &__products-wrapper
        margin-top: 72px
        display: grid
        justify-content: space-between
        grid-template-columns: repeat(3, 330px)
        grid-row-gap: 100px
        margin-bottom: 84px
        &-item
            position: relative
            display: flex
            flex-direction: column
            justify-content: space-between
            height: 380px
            width: 100%
            &_category
                position: absolute
                display: flex
                justify-content: center
                align-items: center
                width: 105px
                height: 34px
                margin: 15px 0 0 14px
                border-radius: 30px
                background: white
                z-index: 20
                & span
                    font-weight: 600
                    font-size: 14px
                    color: #416EF2
            &_favorite
                position: absolute
                opacity: 0
                background: transparent
                right: 30px
                top: 20px
                z-index: 20
                transition: .3s all linear
            &:hover .main-catalog__products-wrapper-item_favorite
                opacity: 1
            &_title
                font-size: 28px
                color: #416EF2
                font-weight: 700
            &_bottom-block
                display: flex
                justify-content: space-between
                align-items: center
            &_button
                font-size: 16px
                border: 2px solid #416EF2
                color: #416EF2
                font-weight: 500
                font-size: 16px
                padding: 15px 27px
                border-radius: 30px
            &_price
                font-weight: 500
                font-size: 20px
                color: black
            &_slider
                width: 100%
                height: 242px
                background-color: #E8EBF5
                border-radius: 30px
                margin-bottom: 13px
                &_slide
                    background-repeat: no-repeat
                    background-position: center
    &__main-category
        width: 100%
        display: flex
        justify-content: center
        margin-bottom: 39px
        & button
            color: white
            font-size: 16px
            padding: 13px 19px
            background: #416EF2
            border-radius: 30px
            border: 2px solid #416EF2
            transition: all .2s linear
            &:hover
                background: transparent
                color: #416EF2

        & + hr
            margin-bottom: 116px

    &__slider
        margin-bottom: 83px
        position: unset !important
        &-sm_title
            color: white
            font-size: 34px
            font-weight: 700
        & hr
            margin-top: 21px
        &-wrapper
            width: 100%
            position: relative
        & .swiper-pagination
            bottom: -50px !important
        & .swiper-button-next.swiper-button-disabled, & .swiper-button-prev.swiper-button-disabled
            border: 2px solid #416EF2
            background: transparent
            opacity: 1
        & .swiper-button-next.swiper-button-disabled
            &:after
                border-color: #416EF2
            &:before
                background: #416EF2
        & .swiper-button-prev.swiper-button-disabled
            &:after
                border-color: #416EF2
            &:before
                background: #416EF2
        & .swiper-button-prev, & .swiper-button-next
            width: 45px
            height: 45px
            background: #416EF2
            border: 2px solid #416EF2
            border-radius: 50%
        & .swiper-button-prev
            left: -124px
        & .swiper-button-next
            right: -124px
        & .swiper-button-next:after
            position: absolute
            right: 15px
            content: ''
            width: 10px
            height: 10px
            border-right: 2px solid white
            border-bottom: 2px solid white
            transform: rotate(-45deg)
        & .swiper-button-next:before
            position: absolute
            content: ''
            width: 15px
            height: 2px
            background: white
            right: 14px

        & .swiper-button-prev:after
            position: absolute
            left: 15px
            content: ''
            width: 10px
            height: 10px
            border-right: 2px solid white
            border-bottom: 2px solid white
            transform: rotate(135deg)
        & .swiper-button-prev:before
            position: absolute
            content: ''
            width: 15px
            height: 2px
            background: white
            left: 13px

        & .swiper-pagination .swiper-pagination-bullet
            background: #416EF2
            width: 15px
            height: 2px
            border-radius: 1px
            &-slide
                margin-top: 21px
                width: 320px !important
                height: 300px !important
                border-radius: 30px
                overflow: hidden
                &_item
                    display: block
                    background-repeat: no-repeat
                    width: 100%
                    height: 100%
        &-slide
            margin-top: 21px
            width: 320px !important
            height: 300px !important
            border-radius: 30px
            overflow: hidden
            &_item
                display: flex
                align-items: flex-end
                padding: 0 0 21px 28px
                background-repeat: no-repeat
                width: 100%
                height: 100%
    &__title
        color: var(--color)
        font-size: 51px
        font-weight: 700
        margin-bottom: 34px
        span
            font-weight: 700
            color: #416EF2
    &-title
        font-size: 51px
        font-weight: 700
    &__links
        width: 800px
        display: flex
        justify-content: space-between
    &-link
        padding: 25px 50px
        font-size: 24px
    &-desc
        padding: 25px 50px
        border: solid 2px white
        border-radius: 10px
        font-size: 22px
    &-suptitle
        font-size: 33px
